import axios from "axios";
import { useEffect, useState } from "react";
import MySearchBar from "../../components/MySearchBar/MySearchBar";
import SingleContent from "../../components/SingleContent/SingleContent";
import "./home.css"
import Fuse from "fuse.js";
import sha1 from "crypto-js/sha1";
import { makeStyles } from "@material-ui/core";
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import InfiniteScroll from "react-infinite-scroll-component"

const useStyles = makeStyles({
    root: {
      width: "100%",
      position: "fixed",
      bottom: 0,
      backgroundColor: "#2d313a",
      zIndex: 100,
    },
    topBtn: {
      width: "50px !important",
      height: "50px !important",
      position: "fixed",
      bottom: "80px",
      right: "10px",
      backgroundColor: "blue",
      zIndex: 100,
      color: "white",
      borderRadius: "50%",
      cursor: "pointer",
    }
  });

export default function Home() {
    const [hasMore, setHasMore] = useState(true);
    const [content, setContent] = useState([]);
    const [cdn, setCdn] = useState();
    const [page, setPage] = useState();
    const [sourcePage, setSourcePage] = useState(0);
    const [source, setSource] = useState([]);
    const [searchText, setSearchText] = useState();
    const [db, setDb] = useState();
    const [manual, setManual] = useState();
    const perPage = 10;
    const classes = useStyles();

    const moreContentClick = () => {
        setPage(page+1);
    };

    const initConfig = () => {
        axios.get(
            `${process.env.REACT_APP_CONFIG_URL}`
        ).then(function(res) {
            setCdn(res.data.cdn);
            axios.get(
                `${process.env.REACT_APP_TEST_BASEDIR}search.json`
            ).then(function(res) {
                setDb(new Fuse(res['data']));
                setPage(1);
                // changePage();
            }).catch(function(err) {
                console.log(err);
            });
        }).catch(function(err) {
            console.log(err);
        });
    };

    const changePage = async () => {
        if (cdn) {
            if (source.length > 0 && typeof source[0] === 'string') {
                // this is search result.
                var tmpContent = []
                for(var i=(page-1)*perPage; i<Math.min(page*perPage, source.length); i++) {
                    const resHash = sha1(source[i]).toString();
                    try {
                        const {data} = await axios.get(
                            `${process.env.REACT_APP_TEST_BASEDIR}output/${resHash}`
                        );
                        tmpContent.push(data);
                    }catch(err) {
                        console.log(err);
                    }
                }
                if (tmpContent.length === 0) {
                    setHasMore(false);
                } else {
                    // not duplicate add same page.
                    if (content.length > 0 && tmpContent.length > 0) {
                        if (content[content.length-1].slug === tmpContent[tmpContent.length-1].slug) {
                            return;
                        }
                    }
                    setContent(content.concat(tmpContent));
                    if (Math.min(page*perPage, source.length) === source.length) {
                        setHasMore(false);
                    } else {
                        setHasMore(true);
                    }
                    // setPage(page+1);
                }
                
            } else {
                if (page * perPage > source.length) {
                    // need more source.
                    const tmpSourcePage = String(sourcePage).padStart(3, '0');
                    try {
                        const {data} = await axios.get(
                            `${process.env.REACT_APP_TEST_BASEDIR}page-${tmpSourcePage}.json`
                        );
                        if (data.length > 0) {

                            const newSource = source.concat(data);
                            setSource(newSource);
                            setContent(content.concat(newSource.slice((page-1)*perPage, page*perPage)));
                            setSourcePage(sourcePage+1);
                            setHasMore(true);
                            // setPage(page+1);
                        } else {
                            setHasMore(false);
                        }
                    }catch(err) {
                        if (source.length > content.length) {
                            setContent(content.concat(source.slice((page-1)*perPage, page*perPage)));
                        }
                        setHasMore(false);
                        console.log(err);
                    }
                } else {
                    // if (content.length > 0) {
                    //     if (content[content.length-1].slug === source[page*perPage-1].slug) {
                    //         setHasMore(false);
                    //         return;
                    //     }
                    // }
                    const newData = source.slice((page-1)*perPage, page*perPage);
                    setContent(content.concat(newData));
                    if (newData.length < perPage) {
                        setHasMore(false);
                    } else {
                        setHasMore(true);
                    }
                    // setPage(page+1);
                }
            }
            
        }
    };

    const searchAction = () => {
        if (!db) {
            console.log('db init failed.');
            return;
        }
        if (searchText && searchText.length !== 0) {
            const tmpResult = db.search(searchText).map((result) => result.item);
            setContent([]);
            setSource(tmpResult);
            setSourcePage(0);
            setPage(1);
            if (page === 1) {
                setManual(!manual);
            }
            if (tmpResult.length > 0) {
                setHasMore(true);
            }
        } else {
            setContent([]);
            setPage(1);
            setSource([]);
            setSourcePage(0);
            setHasMore(false);
            if (page === 1) {
                setManual(!manual);
            }
        }
    };

    useEffect(()=> {        
        console.log('init config');
        initConfig();
    }, []);

    useEffect(() => {
        changePage();
    }, [page, manual]);

    const gotoTop = () => {
        window.scroll(0, 0);
      };

    return (
    <div className="mContent">
      <MySearchBar 
        setSearchText={setSearchText} 
        searchAction={searchAction}
      />
      <div className="trending">
          <InfiniteScroll
          dataLength={content.length}
          next={moreContentClick}
          hasMore={hasMore}
          loader={<h4>~~~正在加载~~~</h4>}
          endMessage = {<h4>~~~我们也是有底线的~~~</h4>}
          style={{overflowX:"hidden"}}
          >
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.slug}
              poster={c.cover}
              title={c.title}
              date={c.update_at}
              duration={c.duration}
              url={c.url}
            />
          ))}
          </InfiniteScroll>
        
      </div>
      {/* <div className="moreContentLink">
        <div className="linkText" onClick={moreContentClick}>{moreText}</div>
      </div> */}
      <KeyboardArrowUpIcon
        className={classes.topBtn}
        onClick={gotoTop}
        />
    </div>
    );
}