import "./Header.css";
import {Link, useLocation} from "react-router-dom";

const Header = () => {
  const location = useLocation();
  return (
    <span onClick={() => window.scroll(0, 0)} className="header">
      <a href="/" ><img src="/logo.png" alt="logo" className="headerLogo" /></a>
      { location.pathname==="/play" ? <Link to="/" className="topBarRightLink">站点首页</Link> : null }
    </span>
  );
};

export default Header;
