// import { Badge } from "@material-ui/core";

import "./SingleContent.css";

import { useHistory } from "react-router";


const SingleContent = ({
  poster,
  title,
  date,
  duration,
  url,
}) => {
  const history = useHistory();
  // const handlePlay = () =>{
  //   history.push({
  //     pathname: "/play",
  //     state: {"url": url, "backurl": backurl, "title": title, }
  //   });
  // };
  function formatSeconds(value) {
    var theTime = parseInt(value);// 秒
    var theTime1 = 0;// 分
    var theTime2 = 0;// 小时
    if (theTime > 60) {
        theTime1 = parseInt(theTime / 60);
        theTime = parseInt(theTime % 60);
        if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
        }
    }
 
    var result = "" + parseInt(theTime);//秒
    if (10 > theTime > 0) {
        result = "0" + parseInt(theTime);//秒
    } else {
        result = "" + parseInt(theTime);//秒
    }
 
    if (10 > theTime1 > 0) {
        result = "0" + parseInt(theTime1) + ":" + result;//分，不足两位数，首位补充0，
    } else {
        result = "" + parseInt(theTime1) + ":" + result;//分
    }
    if (theTime2 > 0) {
        result = "" + parseInt(theTime2) + ":" + result;//时
    }
    return result;
  }
  const formatedDuration = formatSeconds(duration);
  date  = new Date(date);
  // const dateStr = date.getFullYear()+"-"+String((date.getMonth()+1)).padStart(2, '0')+ 
  //   "-"+String(date.getDate()).padStart(2, '0')+" "+String(date.getHours()).padStart(2, '0')+":"+
  //   String(date.getMinutes()).padStart(2, '0')+":"+String(date.getSeconds()).padStart(2, '0');
  const dateStr = date.getFullYear()+"-"+String((date.getMonth()+1)).padStart(2, '0')+ 
    "-"+String(date.getDate()).padStart(2, '0');
  const titleFormat = title.length > 20 ? title.substring(0, 20) + '...' : title;

  const handlePlay = () =>{
    history.push({
      pathname: "/play",
      state: {"url": url, "title": title, "dateStr": dateStr, "duration": formatedDuration, "poster": poster}
    });
  };

  return (
    <div
        className="media"
        style={{ cursor: "pointer" }}
        color="inherit"
        onClick={handlePlay}
      >
      <div className="imageBox">
        <img
          className="poster"
          src={poster}
          alt={title}
        />
        <span className="leftLogo">X10096</span>
        <span className="rightLogo">HD</span>
      </div>
      <div className="videoTimeBox">
        <span className="videoTime">{formatedDuration}</span>
      </div>
      
      <b className="title">{titleFormat}</b>
      <span className="subTitle">
        <span className="subTitle">添加时间：{dateStr}</span>
      </span>
    </div>
  );
};

export default SingleContent;
