import React from "react"
import ReactPlayer from "react-player"
import { useLocation } from "react-router-dom";
import "./Playit.css"

const Playit = () => {
    const location = useLocation();
    const url = location.state.url;
    const title = location.state.title;
    const dateStr = location.state.dateStr;
    const duration = location.state.duration;
    const poster = encodeURI(location.state.poster).replace('(', '%28').replace(')', '%29');

    const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent)
    return (
        <div className="playerContent">
           
            <div className="playTitle">{title}</div>
            <div className="playerBox">
                <ReactPlayer
                className="react-player"
                url={url}
                controls={true}
                playsinline={true}
                width="100%"
                height="300px"
                light={poster}
                playing={true}
                config={{
                    file: {
                        forceHLS:!isSafari,
                        forceVideo: true,
                    }
                }}
                />
                <div className="videoInfoLine">
                    <span className="videoInfoLeft">添加时间：{dateStr}</span>
                    <span className="videoInfoRight">影片时长：{duration}</span>
                </div>
            </div>
            
            
        </div>
    );
};

export default Playit;